@import url(https://fonts.googleapis.com/css2?family=Krona+One&family=Lemonada:wght@700&display=swap);
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
body , html{
    position: relative;
    width: 100%;
    height: 100%;
}
body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen','Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
.joke{
    position: relative;
    width: 100%;
    min-height: 80px;
    padding: 0 5%;
    display: flex;
    align-items: center;
    line-height: 24px;
}
.joke.even{
    background: #0001;
}
.joke i{
    font-size: 20px;
    margin: 0 10px;
    cursor: pointer;
}
.joke p.vote{
    font-size: 18px;
    width: 39px;
    height: 39px;
    color: #000;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    border: 3px solid;
    font-size: 14px;
    box-shadow: 0 0 1px #0009,
                0 0 10px #0005,
                0 0 20px #0003,
                0 0 40px #0001;
}
.joke p.joke-cont{
    position: relative;
    width: 500px;
}
.joke .em{
    font-size: 27px;
}
.main-container{
    position: relative;
    width: 1000px;
    height: 550px;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
}
.main-container .left-container{
    position: relative;
    width: 300px;
    height: 100%;
    background: #9575cd;
    box-shadow: 0 19px 38px rgba(0,0,0,0.3),
                0 15px 12px rgba(0,0,0,0.1),
                inset 0 0 25px #7e57c2;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.main-container .left-container img{
    position: relative;
    width: 60%;
    border: 1px solid #9575cd;
    border-radius: 50%;
    box-shadow: 0 0 5px #110329,
                0 0 10px #2e0e64,
                0 0 20px #5826af;
    margin: 15px 0 30px;
}
.main-container .left-container h1 {
    color: #fff;
    font-family: 'Krona One', sans-serif;
}
.main-container .left-container h1 span{
    font-family: 'Lemonada', cursive;
}
.main-container .left-container button{
    position: relative;
    padding: 10px 20px;
    background: none;
    border: none;
    outline: none;
    border: 2px solid #2e0e64;
    cursor: pointer;
    transition: 0.5s;
    letter-spacing: 1px;
    font-weight: bolder;
    color: #fff;
}
.main-container .left-container button:hover{
    background: #2e0e64;
    box-shadow: 0 0 2px #2e0e64;
}
.main-container .right-container{
    position: relative;
    height: 85%;
    width: 700px;
    background: #fff;
    overflow-y: scroll;
}

::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #ffffff;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(196, 196, 196);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgb(175, 175, 175);
}


/*================================= Loader Section =======================*/
.loader {
    bottom: 0;
    left: 0;
    overflow: hidden;
    position: fixed;
    right: 0;
    top: 0;
    z-index: 99999;
}

.loader-inner {
    bottom: 0;
    height: 60px;
    left: 0;
    margin: auto;
    position: absolute;
    right: 0;
    top: 0;
    width: 100px;
}

.loader-line-wrap {
    -webkit-animation: 
		spin 2000ms cubic-bezier(.175, .885, .32, 1.275) infinite
	;
            animation: 
		spin 2000ms cubic-bezier(.175, .885, .32, 1.275) infinite
	;
    box-sizing: border-box;
    height: 50px;
    left: 0;
    overflow: hidden;
    position: absolute;
    top: 0;
    transform-origin: 50% 100%;
    width: 100px;
}
.loader-line {
    border: 4px solid transparent;
    border-radius: 100%;
    box-sizing: border-box;
    height: 100px;
    left: 0;
    margin: 0 auto;
    position: absolute;
    right: 0;
    top: 0;
    width: 100px;
}
.loader-line-wrap:nth-child(1) { -webkit-animation-delay: -50ms; animation-delay: -50ms; }
.loader-line-wrap:nth-child(2) { -webkit-animation-delay: -100ms; animation-delay: -100ms; }
.loader-line-wrap:nth-child(3) { -webkit-animation-delay: -150ms; animation-delay: -150ms; }
.loader-line-wrap:nth-child(4) { -webkit-animation-delay: -200ms; animation-delay: -200ms; }
.loader-line-wrap:nth-child(5) { -webkit-animation-delay: -250ms; animation-delay: -250ms; }

.loader-line-wrap:nth-child(1) .loader-line {
    border-color: hsl(0, 80%, 60%);
    height: 90px;
    width: 90px;
    top: 7px;
}
.loader-line-wrap:nth-child(2) .loader-line {
    border-color: hsl(60, 80%, 60%);
    height: 76px;
    width: 76px;
    top: 14px;
}
.loader-line-wrap:nth-child(3) .loader-line {
    border-color: hsl(120, 80%, 60%);
    height: 62px;
    width: 62px;
    top: 21px;
}
.loader-line-wrap:nth-child(4) .loader-line {
    border-color: hsl(180, 80%, 60%);
    height: 48px;
    width: 48px;
    top: 28px;
}
.loader-line-wrap:nth-child(5) .loader-line {
    border-color: hsl(240, 80%, 60%);
    height: 34px;
    width: 34px;
    top: 35px;
}

@-webkit-keyframes spin {
    0%, 15% {
		transform: rotate(0);
	}
	100% {
		transform: rotate(360deg);
	}
}

@keyframes spin {
    0%, 15% {
		transform: rotate(0);
	}
	100% {
		transform: rotate(360deg);
	}
}
.App{
    position: relative;
    width: 100%;
    height: 100vh;
    background : linear-gradient(
        135deg ,
        rgb(0 0 0) 0%,
        rgb(84 84 84) 50%,
        #6b8bd6  50%,
        #041b51  100%
    );
    display: flex;
    justify-content: center;
    align-items: center;
}
